button.btn-bc {
    @extend .smoothPointer;
    min-width: 7.188rem;
    padding: 0.75rem;
    border-radius: 1.25rem;
    background-color: $Button_BG;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 0.063rem;
    font-weight: 700;
    &:hover {
        filter: brightness(.9);
    }
    &:active {
        transform: scale(.93);
    }
    @media (max-width: 85.375rem) {
        & {
            width: 6.25rem;
            font-size: .75rem;
        }
    }
    @media (max-width: 40rem) {
        & {
            width: 4.063rem;
            font-size: 0.688rem;
        }
    }
}