.gameInside {
    height: 100%;
    .container {
        width: 50rem;
        @media (max-width: 85.375rem) {
            & {
                width: 43.75rem;
            }
        }
        @media (max-width: 62rem) {
            & {
                width: 38.75rem;
            }
        }
        @media (max-width: 40rem) {
            & {
                width: 100%;
            }
        }
        header {
            width: 100%;
            height: 15vh;
            display: flex;
            justify-content: space-between;
            align-items: center;
            transform: translateY(-3rem);
            opacity: 0;
            transition: .5s;
            &.showGame {
                transform: translateY(0);
                opacity: 1;
            }
            @media (max-width: 40rem) {
                & {
                    padding: 0 0.625rem;
                }
            }
            img {
                height: 4.375rem;
                @media (max-width: 85.375rem) {
                    & {
                        height: 3.438rem;
                    }
                }
                @media (max-width: 40rem) {
                    & {
                        height: 1.875rem;
                    }
                }
            }
        }
        .content {
            position: relative;
            margin-top: 2rem;
            @media (max-width: 85.375rem) {
                & {
                    margin-top: 0.625rem;
                }
            }
            @media (max-width: 62rem) {
                & {
                    margin-top: 0;
                }
            }
            .playersPanel {
                display: flex;
                justify-content: space-between;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                opacity: 0;
                width: 90%;
                transition: .5s;
                @media (min-width: 62rem) {
                    &.showGame {
                        width: 150%;
                        opacity: 1;
                    }
                }
                @media (max-width: 85.375rem) {
                    & {
                        width: 135%;
                    }
                }
                @media (max-width: 62rem) {
                    & {
                        position: unset;
                        width: 100%;
                        transform: translateY(-2rem);
                    }
                    &.showGame {
                        transform: translateY(0);
                        opacity: 1;
                    }
                }
                @media (max-width: 40rem) {
                    & {
                        padding: 0 0.625rem;
                    }
                }
                .firstPlayer,
                .secondPlayer {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    background-color: #fff;
                    padding: 2.85rem 1.75rem 1.65rem;
                    border-radius: 1.25rem;
                    border: 0.188rem solid #000;
                    box-shadow: 0 0.563rem 0 #000;
                    position: relative;
                    animation: 2s flow infinite alternate-reverse;
                    @media (max-width: 85.375rem) {
                        & {
                            transform: scale(.85);
                        }
                    }
                    @media (max-width: 62rem) {
                        & {
                            padding: 0;
                            height: 7.5rem;
                            width: 18.75rem;
                            margin-bottom: 1rem;
                            justify-content: space-evenly;
                            animation: unset;
                        }
                    }
                    @media (max-width: 40rem) {
                        & {
                            padding: 0 1rem;
                            height: 5rem;
                            width: 11.25rem;
                        }
                    }
                    .icon {
                        @extend .center-g;
                        height: 3.125rem;
                        width: 3.125rem;
                        border: 0.188rem solid #000;
                        box-shadow: 0 0.313rem 0 #000;
                        border-radius: 50%;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        top: -1.563rem;
                        .happyFace {
                            transform: scale(1.2);
                        }
                        .crown {
                            position: relative;
                            img {
                                position: absolute;
                                width: 3.75rem;
                                top: -5.125rem;
                                left: 50%;
                                transform: translateX(-50%);
                            }
                        }
                        @media (max-width: 62rem) {
                            & {
                                left: -1.563rem;
                                top: 50%;
                                transform: translateY(-50%) scale(1.2);
                                
                            }
                        }
                        @media (max-width: 40rem) {
                            & {
                                transform: translateY(-50%) scale(.85);
                            }
                        }
                    }
                    .playerNum {
                        font-size: 1.25rem;
                        font-weight: 600;
                        text-transform: uppercase;
                        margin-bottom: 0.45rem;
                        @media (max-width: 62rem) {
                            & {
                                text-wrap: nowrap;
                            }
                        }
                        @media (max-width: 40rem) {
                            & {
                                font-size: 1rem;
                            }
                        }
                    }
                    > span {
                        font-size: 3rem;
                        font-weight: 500;
                        transform: scale(1.2);
                        @media (max-width: 40rem) {
                            & {
                                font-size: 2.5rem;
                            }
                        }
                    }
                }
                .firstPlayer {
                    @media (max-width: 62rem) {
                        & {
                            flex-direction: row;
                            .playerNum {
                                margin: 0;
                            }
                        }
                    }
                    .icon {
                        background-color: $red;
                    }
                }
                .secondPlayer {
                    @media (max-width: 62rem) {
                        & {
                            flex-direction: row-reverse;
                            .playerNum {
                                margin: 0;
                            }
                        }
                    }
                    .icon {
                        background-color: $yellow;
                        .happyFace {
                            transform: scale(1.2) rotateY(180deg);
                        }
                        @media (max-width: 62rem) {
                            & {
                                left: unset;
                                right: -1.563rem;
                            }
                        }
                    }
                }
            }
            .gamePanel {
                @extend .center-g;
                transform: scale(0);
                opacity: 0;
                transition: .5s;
                &.showGame {
                    transform: scale(1);
                    opacity: 1;
                }
                .panel_Wrapper {
                    width: 42.813rem;
                    background-color: #fff;
                    border: 0.188rem solid #000;
                    border-radius: 1.563rem;
                    box-shadow: 0 0.563rem 0 #000;
                    padding: 0.625rem 0.625rem 2.625rem;
                    z-index: 9;
                    @media (max-width: 85.375rem) {
                        & {
                            width: 28.75rem;
                        }
                    }
                    @media (max-width: 62rem) {
                        & {
                            width: 38.438rem;
                        }
                    }
                    @media (max-width: 40rem) {
                        & {
                            width: 21.875rem;
                            padding: 0.625rem 0.625rem 1.625rem;
                        }
                    }
                    .disks_Container {
                        display: flex;
                        flex-wrap: wrap;
                        &.First_Player_Turn div:hover {
                            background-color: $red;
                        }
                        &.Second_Player_Turn div:hover {
                            background-color: $yellow;
                        }
                        div {
                            @extend .smoothPointer;
                            height: 4.375rem;
                            width: 4.375rem;
                            border: 0.188rem solid #000;
                            border-radius: 50%;
                            margin: .75rem;
                            background-color: $Background_Color;
                            box-shadow: inset 0 0.5rem 0 #000;
                            &.first {
                                background-color: $red;
                            }
                            &.second {
                                background-color: $yellow;
                            }
                            &.first, 
                            &.second {
                                pointer-events: none;
                            }
                            &.first:hover {
                                background-color: $red;
                            }
                            &.second:hover {
                                background-color: $yellow;
                            }
                            &:active {
                                transform: scale(.95);
                            }
                            @media (max-width: 85.375rem) {
                                & {
                                    height: 3.125rem;
                                    width: 3.125rem;
                                    margin: 0.375rem;
                                }
                            }
                            @media (max-width: 62rem) {
                                & {
                                    height: 3.75rem;
                                    width: 3.75rem;
                                    margin: 0.375rem 0.75rem;
                                }
                            }
                            @media (max-width: 40rem) {
                                & {
                                    height: 2.5rem;
                                    width: 2.5rem;
                                    margin: 0.25rem 0.188rem;
                                }
                            }
                        }
                    }
                }
            }
            .counter {
                @extend .center-g;
                position: absolute;
                left: 50%;
                bottom: -2.5rem;
                transform: translateX(-50%);
                z-index: 99;
                opacity: 0;
                transition: .8s;
                &.showGame {
                    opacity: 1;
                }
                @media (max-width: 85.375rem) {
                    & {
                        bottom: -1.25rem;
                        transform: translateX(-50%) scale(.85);
                    }
                }
                @media (max-width: 40rem) {
                    & {
                        bottom: -1.875rem;
                        transform: translateX(-50%) scale(0.7);
                    }
                }
                svg {
                    height: 9.375rem;
                    position: absolute;
                    transition: .5s;
                }
                .info_wrapper {
                    position: relative;
                    @extend .center-g;
                    .info {
                        position: absolute;
                        text-align: center;
                        transform: translateY(0.625rem);
                        color: #fff;
                        .current_player {
                            text-wrap: nowrap;
                            font-weight: 500;
                            text-transform: uppercase;
                            margin-bottom: 0.375rem;
                        }
                        .time {
                            font-size: 3rem;
                            font-weight: 600;
                            text-transform: lowercase;
                        }
                    }
                }
            }
            .winMsg {
                background-color: #fff;
                position: absolute;
                left: 50%;
                bottom: -6.5rem;
                transform: translateX(-50%);
                width: 18.75rem;
                height: 9.375rem;
                border: 0.188rem solid #000;
                border-radius: 1.25rem;
                box-shadow: 0 0.438rem 0 #000;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                opacity: 0;
                transition: .5s;
                p {
                    font-size: 1.25rem;
                    text-transform: uppercase;
                    font-weight: 600;
                }
                span {
                    font-size: 3rem;
                    text-transform: uppercase;
                    font-weight: 700;
                }
                @media (max-width: 85.375rem) {
                    & {
                        bottom: -5.5rem;
                        transform: translateX(-50%) scale(.75);
                    }
                }
                @media (max-width: 40rem) {
                    & {
                        bottom: -6rem;
                        transform: translateX(-50%) scale(0.55);
                    }
                }
            }
        }
    }
}

@keyframes flow {
    0% {
        transform: translateY(-1rem);
    }
    100% {
        transform: translateY(0);
    }
}